:root {
  --sidebar-width: 28em;
  --ui-opacity: 0.7;
  --primary-color: rgb(240, 88, 240);
  --ui-bg-color: rgba(22, 22, 22, 0.85);
  --primary-text-color: #fbfbfb;
}

body {
  margin: 0;
  padding: 0;
  /* Verdana, Geneva, Tahoma,  */
  font-family: sans-serif;
  color: var(--primary-text-color);
  overflow: hidden;
}

/* Responsiveness rules */

body {
  font-size: xx-large;
}
@media screen and (max-width: 3300px) {
  body {
    font-size: x-large;
  }
}
@media screen and (max-width: 3000px) {
  body {
    font-size: larger;
  }
}
@media screen and (max-width: 2600px) {
  body {
    font-size: large;
  }
}
@media screen and (max-width: 1900px) {
  body {
    font-size: medium;
  }
}
@media screen and (max-width: 1100px) {
  body {
    font-size: smaller;
  }
}
@media screen and (max-width: 760px) {
  body {
    font-size: small;
  }
}
@media screen and (max-width: 640px) {
  body {
    font-size: x-small;
  }
} 
@media screen and (max-width: 420px) {
  body {
    font-size: xx-small;
  }
}

canvas {
  display: block;
}

#root, #fps {
  opacity: var(--ui-opacity);
}

.reset-ghosting-btn {
  position: fixed;
  left: 0;
  bottom: 0;
  margin: .3em;
  padding: .3em .6em;
  color: rgb(192, 128, 192);
  border: 2px solid rgb(114, 15, 114);
  border-radius: 9px;
  background-color: rgb(94, 13, 94);
  cursor: pointer;
  z-index: 1;
}

.counters {
  position: fixed;
  display: flex;
  justify-content: center;
  left: 0;
  top: 0;
  width: 100%;
}

.counters-container {
  display: inline-flex;
  padding: .3em .6em;
  font-size: 1em;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

.counters-container div {
  margin: .2em .2em .1em .2em;
  padding: .1em .2em 0 .2em;
}

.counters-container .counter {
  display: flex;
  flex-direction: column;
}

.counters-container .counter div {
  display: flex;
  justify-content: center;
}

.counters-container .counter div.label {
  margin: 0em;
  padding: 0em;
  font-size: .8em;
  font-weight: bold;
  color: var(--primary-color)
}

.counters-container .counter div.value {
  font-family: monospace;
  font-size: 1.2em;
}

.controls {
  position: fixed;
  display: flex;
  justify-content: center;
  left: 0;
  bottom: 0;
  width: 100%;
}

.controls-container {
  display: inline-flex;
  align-items: center;
  padding: .8em .9em .6em .9em;
  font-size: 1em;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.controls-container div {
  margin: .3em;
  padding: .3em .6em;
}

.counters-container,
.controls-container,
.sidebar-container {
  background-color: var(--ui-bg-color)
}

.controls-container label {
  font-size: .9em;
  color: var(--primary-text-color);
}

.controls-container input:nth-of-type(1) {
  margin-right: 1em;
}

.counters-container,
.controls-container,
.sidebar-container {
  border: 1px solid rgba(255, 255, 255, 0.2)
}
