/* Sidebar */
.sidebar {
  position: fixed;
  display: flex;
  height: 100%;
  width: var(--sidebar-width);
  transition: .250s ease-in-out;
}

.sidebar.hidden {
  right: -300px
}

.sidebar.left-sidebar {
  top: 0;
  left: 0;
}

.sidebar.right-sidebar {
  top: 0;
  right: 0;
}

.sidebar.left-sidebar.closed {
  left: calc(-1 * var(--sidebar-width));
}

.sidebar.right-sidebar.closed {
  right: calc(-1 * var(--sidebar-width));
}

.sidebar-container {
  display: flex;
  flex-direction: column;
  padding: 0 .9em;
  width: 100%;
  font-size: 1em;
}

.sidebar-container .title h3 {
  font-size: 1.7em;
  font-weight: bold;
  letter-spacing: .3em;
  margin-top: .9em;
}

.sidebar .title {
  display: flex;
  flex-direction: row;
}

.sidebar.left-sidebar .title {
  justify-content: flex-end;
}

.sidebar.right-sidebar .title {
  justify-content: flex-start;
}

.sidebar-container > div,
.sidebar-container > div > div,
.sidebar-container > div > div > div {
  display: flex;
  /* align-items: center; */
  /* justify-content: space-between; */
  flex-direction: column;
  margin: .3em;
  padding: .3em .6em;
}

.sidebar-container div select {
  width: -webkit-fill-available;
}

.sidebar-container label {
  flex-wrap: nowrap;
  font-size: .9em;
}

.sidebar-container div input {
  width: -webkit-fill-available;
}

.menu-btn {
  position: fixed;
  display: inline-flex;
  top: 0;
  margin: .7em 1.1em;
  font-size: 2em;
  cursor: pointer;
  z-index: 1;
}

.menu-btn.menu-btn-left {
  justify-content: flex-start;
  left: 0;
}

.menu-btn.menu-btn-right {
  justify-content: flex-end;
  right: 0;
}

.menu-btn div {
  padding: .3em;
}

.objectsContainer {
  overflow-y: scroll;
}

.object {
  padding: .43em .66em;
  border-radius: 9px;
  border: 1px solid #333;
}

.objectsContainer input {
  border: 1px solid #333;
  background-color: black;
  padding: .43em .66em;
}

.menu-btn > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu-btn .menu-title {
  font-size: .5em;
}

.object input {
  font-family: monospace;
  color: #999;
}

.unlocked .object input {
  color: #ccc;
}

.btn {
  left: 0;
  bottom: 0;
  margin: .3em .6em;
  padding: .3em .6em;
  color: rgb(192, 128, 192);
  border: 2px solid rgb(114, 15, 114);
  border-radius: 9px;
  background-color: rgba(94, 13, 94, 0.1);
  cursor: pointer;
  z-index: 1;
}

.scenarios-dropdown {
  margin-bottom: 1em;
}


